import React from 'react';
import './ColumnHeader.scss';
import { FaChevronUp, FaChevronDown } from 'react-icons/fa';

export default function ColumnHeader({ title, sortable, sortableState }) {
  const [sort, setSort] = React.useState(sortableState);
  return (
    <React.Fragment>
      <div className="data-table__col-header">
        <span className="col-header__title">{title}&nbsp;&nbsp;</span>
        {
          sortable ?
            <React.Fragment>
              <FaChevronUp className={"col-header__icon " + (sort ? "active" : "")} />
              <FaChevronDown className={"col-header__icon " + (sort ? "" : "active")} />
            </React.Fragment>
            : null
        }
      
      </div>
    </React.Fragment>
  );
  
}